.contactsBlock {
  min-height: 150vh;
  background-image: url(https://i0.wp.com/demo.softhopper.studio/selfer/wp-content/uploads/sites/20/2018/10/bg-man-wall.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  position: relative;
  padding-bottom: 30px;

  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #020312;
    opacity: 0.92;
  }

  @media screen and (max-width: 670px) {
    padding-bottom: 30px;
  }

  .contactsContainer {
    flex-direction: column;
    justify-content: flex-start;

    .adressContainer {
      position: relative;
      z-index: 1;
      display: flex;
      width: 100%;
      margin: 0 0 45px 0;
      padding: 0 6rem 0 0;
      @media screen and (max-width: 670px) {
        align-items: center;
        margin: 0;
        flex-direction: column;
        padding: 0;
      }
    }

    .title {
      position: relative;
      z-index: 1;
      margin: 0 0 45px 0;
      color: #ffffff;
      font-family: 'Rubik', sans-serif;
      font-size: 1.1rem;
      font-weight: 500;
      display: flex;
      width: 100%;
      @media screen and (max-width: 670px) {
        justify-content: center;
      }
    }

    .contactIcons {
      position: relative;
      z-index: 1;
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin: 0 0 45px 0;
      @media screen and (max-width: 670px) {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 6em;
      }
    }

    .formContainer {
      display: flex;
      flex-direction: column;
      position: relative;
      z-index: 1;
      width: 100%;
      justify-content: space-between;
      flex-wrap: wrap;

      .nameEmail {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        @media screen and (max-width: 456px) {
          flex-direction: column;
        }

        .name {
          display: flex;
          flex: 0 0 50%;
          flex-direction: column;
          padding: 15px;
          margin-left: -15px;
          @media screen and (max-width: 456px) {
            margin-right: -15px;
          }

          span {
            color: white;
            margin-bottom: 5px;
          }

          input {
            height: 40px;
            color: #828282;
            background: rgba(255, 255, 255, 0.1);
            border: 0;
            outline: none;
          }
        }

        .email {
          display: flex;
          flex: 0 0 50%;
          flex-direction: column;
          padding: 15px;
          margin-right: -15px;
          @media screen and (max-width: 456px) {
            margin-left: -15px;
          }

          span {
            color: white;
            margin-bottom: 5px;
          }

          input {
            height: 40px;
            color: #828282;
            background: rgba(255, 255, 255, 0.1);
            border: 0;
            outline: none;
          }
        }
      }

      .subject {
        display: flex;
        flex-direction: column;
        padding: 15px 0 15px 0;

        span {
          color: white;
          margin-bottom: 5px;
        }

        input {
          height: 40px;
          color: #828282;
          background: rgba(255, 255, 255, 0.1);
          border: 0;
          outline: none;
        }
      }

      .message {
        display: flex;
        flex-direction: column;
        padding: 15px 0 15px 0;

        span {
          color: white;
          margin-bottom: 5px;
        }

        textarea {
          height: 100px;
          color: #828282;
          background: rgba(255, 255, 255, 0.1);
          border: 0;
          outline: none;
        }
      }
    }

    .buttonContainer {
      display: flex;
      justify-content: flex-end;
      width: 100%;
      @media screen and (max-width: 670px) {
        justify-content: center;
      }
    }
  }
}











