.title {
  text-align: center;
  margin: 30px 0px 30px 0px;
  position: relative;
  z-index: 1;

  h2 {
    font-family: 'Rubik', sans-serif;
    color: #ffffff;
    font-size: 1.875rem;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 1.5em;
    letter-spacing: 0.015px;
    margin: 0
  }

  .textCenter {
    position: relative;
    display: inline-block;
    margin-bottom: 30px;

    &::before {
      content: "";
      width: 25px;
      height: 1px;
      position: absolute;
      left: -50px;
      top: 12px;
      opacity: 0.75;
      background: #f26c4f;
    }

    &::after {
      content: "";
      width: 25px;
      height: 1px;
      position: absolute;
      right: -50px;
      top: 12px;
      opacity: 0.75;
      background: #f26c4f;
    }

    span:nth-child(1) {
      position: absolute;
      width: 2px;
      height: 25px;
      border-right: 2px dotted;
      transform: rotate(45deg);
      border-right-color: #f26c4f
    }

    span:nth-child(2) {
      position: absolute;
      width: 2px;
      height: 25px;
      border-right: 2px dotted;
      transform: rotate(-45deg);
      border-right-color: #f26c4f
    }
  }
}










