.inner {
  display: flex;
  flex-direction: column;
  flex: 1rem 1rem 0;
  width: 200px;
  height: 150px;
  @media screen and (max-width: 670px) {
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
  }

  .innerLogo {
    margin-bottom: 20px;
    @media screen and (max-width: 670px) {
      margin-bottom: 10px;
    }
  }

  .innerTitle {
    color: white;
    font-family: 'Rubik', sans-serif;
    font-size: 1.125rem;
    font-weight: 400;
    margin-bottom: 10px;
  }

  .innerDescription {
    font-family: 'Open Sans', sans-serif;
    color: #dfdfdf;
    font-size: 0.875rem;
  }
}